/*! typey | GPLv2 License | https://github.com/jptaranto/typey */
.block-superfishmain {
  position: absolute;
  top: 0;
  right: 0;
}

@media (min-width: 1200px) {
  .block-superfishmain {
    position: static;
    width: unset;
    float: right;
    max-width: calc(100% - 140px);
  }
}

.block-superfishmain .sf-main {
  margin: 0;
  display: flex;
  justify-content: flex-end;
}

.block-superfishmain .sf-main.sf-expanded {
  flex-direction: column;
}

.block-superfishmain .sf-main a {
  padding: 1rem;
  font-size: 1.1875rem;
  color: #a32a29;
}

.block-superfishmain .sf-main a:hover, .block-superfishmain .sf-main a:active, .block-superfishmain .sf-main a:focus {
  color: #7a201f;
}

.block-superfishmain .sf-main a.is-active {
  color: #73984a;
}

.block-superfishmain .sf-main .sf-has-clone-parent {
  padding-left: 0.5rem;
}

@media (min-width: 1400px) {
  .block-superfishmain .sf-main .sf-clone-parent {
    display: none;
  }
}

.block-superfishmain .sf-main .sf-clone-parent > a {
  padding-left: 1em;
  border-bottom: 2px #a32a29 solid;
}

.block-superfishmain .sf-main .sf-clone-parent > a::after {
  content: ' 〉';
}

.block-superfishmain .sf-main .sf-clone-parent > a:hover, .block-superfishmain .sf-main .sf-clone-parent > a:active, .block-superfishmain .sf-main .sf-clone-parent > a:focus {
  background-color: #eeeeee;
}

.block-superfishmain .sf-main .active-trail > a {
  color: #73984a;
}

.block-superfishmain .sf-depth-1 {
  flex: 1 1 auto;
  text-transform: uppercase;
}

.block-superfishmain .sf-depth-1:last-of-type > a {
  padding-right: 0;
}

.block-superfishmain .sf-depth-2 {
  background-color: #fff;
  padding-left: 1em;
}

.block-superfishmain .sf-depth-2 a {
  text-transform: none;
}

.block-superfishmain .sf-depth-2 a:hover, .block-superfishmain .sf-depth-2 a:active, .block-superfishmain .sf-depth-2 a:focus {
  background-color: #eeeeee;
}

#superfish-main-toggle {
  position: relative;
  font-size: 11.25px !important;
  float: right;
  background: #ffb819;
  color: #fff;
  border: 0;
  padding: 2.5em 0.75em 0.25em;
}

#superfish-main-toggle span {
  text-transform: uppercase;
}

#superfish-main-toggle::after {
  display: none;
}

#superfish-main-toggle::before {
  position: absolute;
  top: 0;
  left: 50%;
  content: '≡';
  font-size: 4em;
  line-height: 0.75em;
  color: #fff;
  transform: translateX(-50%);
}

#superfish-main-toggle.sf-expanded {
  background: none;
}

#superfish-main-toggle.sf-expanded span {
  display: none;
}

#superfish-main-toggle.sf-expanded::before {
  content: '×';
  font-weight: 300;
  font-family: Helvetica, sans-serif;
  color: #a32a29;
}

#superfish-main-accordion {
  max-width: 50vw;
  float: right;
}
